	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/website-screenshots-made-easy/index.md absPath of file >>> MarkdownRemark","html":"<p>Really easy, in fact. <a href=\"http://www.websitescreenshots.com/\" title=\"Open this link in a new window.\">www.websitescreenshots.com</a> has a downloadable program by <a href=\"http://www.nathanm.com/\">Nathan Moinvaziri</a> which is simply awesome. It ties into Internet Explorer, and uses it to capture a full or part screenshot of any website. It has all the native features your own Internet Explorer has, so Flash and Javascript and the like, all the features you need (cropping, resizing, resampling) and even full-page view. It's also free. (but without command line abilities) I found it while searching for some sort of wordpress plugin to replace my reliance on the Snapshots rollovers. Oh, I know such a plugin could exist, but it doesn't. Even so, getting screenshots on the web is now as easy as Open WebShot > Enter URL > Capture > Upload > Send to Editor > Publish.</p>\n<p><img src=\"/content/images/2007/06/websitescreenshotscom.jpg\" alt=\"websitescreenshotscom.jpg\"></p>","frontmatter":{"title":"Website screenshots made easy.","date":"June 05, 2007","tags":["external-links","downloadable-applications","screenshots","tools"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/flash-16bit-colour-blendmodes-and-cacheasbitmap-a-recipie-for-disaster/","tags":["actionscript","flash"],"title":"Flash, 16bit colour, blendModes and cacheAsBitmap.  A recipie for disaster?","image":null,"date":"2007-06-05T12:07:12.000Z"}},"next":{"frontmatter":{"path":"/blog/wordpress-design-competition/","tags":["design","wordpress","competitions"],"title":"Wordpress design competition...","image":null,"date":"2007-06-07T13:13:22.000Z"}}}}

